import clsx from 'clsx';
import PropTypes from 'prop-types';
import React from 'react';

import './Checkbox.scss';

/**
 *  Checkbox    .
 *
 * @component
 * @param {Object} props -  .
 * @param {string} props.className -  CSS     .
 * @param {string} props.name -  .
 * @param {function} props.onClick -        .
 * @param {boolean} props.checked -   .
 * @param {boolean} props.disabled - ,     .
 * @param {ReactNode} props.children -  .
 * @returns {JSX.Element} -  JSX-  Checkbox.
 */
const Checkbox = ({ className, name, onClick, children, checked = false, disabled = false }) => {
  /**
   *     .
   * @param {Event} event -  .
   */
  const handleClick = (event) => {
    if (disabled) return;
    if (onClick && event.target.tagName.toLowerCase() !== 'input') {
      onClick(event);
    }
  };

  return (
    <label
      onClick={handleClick}
      className={clsx('checkbox', className, disabled && 'checkbox_disabled')}
    >
      <input
        className="checkbox__input"
        type="checkbox"
        disabled={disabled}
        name={name}
        checked={checked}
        onChange={() => {}}
      />
      <span
        className={clsx(
          'checkbox__checkbox',
          checked && 'checkbox__checkbox_active',
          disabled && 'checkbox_disabled'
        )}
      ></span>
      {children}
    </label>
  );
};

Checkbox.propTypes = {
  className: PropTypes.string,
  name: PropTypes.string,
  onClick: PropTypes.func,
  checked: PropTypes.bool,
  disabled: PropTypes.bool,
  children: PropTypes.node,
};

export { Checkbox };
