import clsx from 'clsx';
import PropTypes from 'prop-types';
import React from 'react';

import './AppImage.scss';

/**
 *    .
 *
 * @param {string} imageSrc - URL .
 * @param {string} imageAlt -   .
 * @param {string} className -   CSS.
 * @param {boolean} border -     ().
 * @param {string} radius -   (, :'radius-20', 'radius-30', 'radius-50').
 * @param loading
 * @returns {JSX.Element}
 */
const AppImage = ({ imageSrc, imageAlt, className, border, radius, loading }) => {
  return (
    <img
      className={clsx(
        'app-image',
        className,
        radius && `app-image_${radius}`,
        border && `app-image_border`
      )}
      src={imageSrc}
      loading={loading}
      alt={imageAlt}
    />
  );
};
AppImage.propTypes = {
  imageSrc: PropTypes.string.isRequired,
  imageAlt: PropTypes.string,
  className: PropTypes.string,
  border: PropTypes.bool,
  radius: PropTypes.oneOf(['radius-20', 'radius-30', 'radius-50']),
};

AppImage.defaultProps = {
  imageAlt: ' ',
  className: '',
  border: false,
  radius: '',
};
export { AppImage };
