import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';

import { AppApi } from '../../shared/api';

import './EmailConfirmation.scss';

const EmailConfirmation = () => {
  const [confirmationStatus, setConfirmationStatus] = useState('pending');
  const [error, setError] = useState(null);
  const { token, uid } = useParams();

  useEffect(() => {
    const confirmEmail = async () => {
      try {
        const response = await AppApi.auth.activate({ token, uid });

        if (response.token) {
          setConfirmationStatus('error');
          setError('   . ,  .');
        } else if (response.uid) {
          setConfirmationStatus('error');
          setError('      .');
        } else {
          setConfirmationStatus('success');
        }
      } catch (error) {
        console.error('  ', error);
        setConfirmationStatus('error');
        setError(error.messages.error || '    .');
      }
    };

    confirmEmail();
  }, [token]);

  return (
    <div className="email-confirmation">
      {confirmationStatus === 'success' ? (
        <p className="email-confirmation__text">
                .
        </p>
      ) : confirmationStatus === 'error' ? (
        <div>
          <p className="email-confirmation__error">{error}</p>
          {error === '   . ,  .' ? (
            <p className="email-confirmation__text">{error}</p>
          ) : (
            <p className="email-confirmation__text">
              ! -         .
            </p>
          )}
        </div>
      ) : (
        <p className="email-confirmation__text">    ...</p>
      )}
    </div>
  );
};

export default EmailConfirmation;
