import clsx from 'clsx';
import PropTypes from 'prop-types';
import React from 'react';
import { Link } from 'react-router-dom';

import './AppLink.scss';

/**
 *   (Link  NavLink)     .
 *
 * @param {string} className -   CSS.
 * @param {ReactNode} children -  .
 * @param {string} target -  target  .
 * @param {string} to - URL,     .
 * @param {string} type -   (, 'gray', 'active').
 * @param {function} onClick - CallBack 
 * @returns {JSX.Element}
 * @constructor
 */
const AppLink = ({ className, children, target, to, type, onClick }) => {
  return (
    <Link
      onClick={onClick}
      target={target}
      className={clsx('app-link', type && `app-link_${type}`, className)}
      to={to}
    >
      {children}
    </Link>
  );
};

AppLink.propTypes = {
  className: PropTypes.string,
  children: PropTypes.node,
  target: PropTypes.string,
  to: PropTypes.string.isRequired,
  type: PropTypes.oneOf(['gray', 'dark']),
};

AppLink.defaultProps = {
  className: '',
  target: '',
  type: 'gray',
  to: '#',
};

export { AppLink };
