/**
 *      
 *
 * @param {number} number - ,     
 * @param {string} one -     (, "")
 * @param {string} two -    2, 3, 4 (, "")
 * @param {string} few -    5   (, "")
 * @returns {string} -         
 */
export function getNoun(number, one, two, few) {
  let n = Math.abs(number);
  n %= 100;
  if (n >= 5 && n <= 20) {
    return few;
  }
  n %= 10;
  if (n === 1) {
    return one;
  }
  if (n >= 2 && n <= 4) {
    return two;
  }
  return few;
}

/**
 *    Unix timestamp    " "
 *
 * @param unixTimestamp
 * @returns {string}
 */
export function formatDateUnixTimestamp(unixTimestamp) {
  const months = [
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
  ];

  const date = new Date(unixTimestamp * 1000); //   
  const day = date.getDate();
  const month = months[date.getMonth()];

  return `${day} ${month}`;
}

/**
 *       
 *
 * @param productList
 * @returns {{totalQuantity: number, totalPrice: number}|*}
 */
export function getCalculateProductInfo(productList) {
  if (!productList.length) {
    return { totalQuantity: 0, totalPrice: 0 };
  }

  return productList.reduce(
    (result, currentProduct) => {
      const productPrice = parseFloat(currentProduct.price.replace(/\s/g, ''));
      const productQuantity = parseFloat(currentProduct.quantity);

      result.totalQuantity += productQuantity;
      result.totalPrice += Math.round(productPrice * productQuantity);
      return result;
    },
    { totalQuantity: 0, totalPrice: 0 }
  );
}

/**
 *    10000  10 000,   
 *
 * @param price - 
 * @returns {string}
 */
export function priceFormat(price) {
  return new Intl.NumberFormat('ru-RU').format(parseFloat(price));
}

/**
 *    88008000000  8-800-800-00-00
 *
 * @param phoneNumber
 * @returns {*}
 */
export function formatPhoneNumber(phoneNumber) {
  //   ,  
  const cleanPhoneNumber = phoneNumber.replace(/\D/g, '');

  //         
  return cleanPhoneNumber.replace(/(\d{1})(\d{3})(\d{3})(\d{2})(\d{2})/, '$1-$2-$3-$4-$5');
}

/**
 *  
 *
 * @param originalDate
 * @returns {string}
 */
export function formatDateAsDDMMYY(originalDate) {
  const date = new Date(originalDate);
  const day = date.getDate().toString().padStart(2, '0');
  const month = (date.getMonth() + 1).toString().padStart(2, '0');
  const year = date.getFullYear().toString().slice(-2);
  return `${day}.${month}.${year}`;
}

/**
 *   
 *
 * @param status
 * @returns {*|string}
 */
export function getStatusName(status) {
  const statusMap = {
    Created: '',
    Updated: '',
    Paid: '',
    Transit: ' ',
    Received: '',
    Canceled: '',
    Returned: '',
  };

  // ,      statusMap
  if (status in statusMap) {
    return statusMap[status];
  } else {
    return ' ';
  }
}
